import React from 'react'

const RecentCard = (props) => {
    const {index, namaResep, dateAdded} = props;
    return (
        <div key={index + 1} className="recent-card">

            <i className="fa-solid fa-bowl-food"></i>
            <header>
                <p>{namaResep}</p>
                <p className='date'>{dateAdded}</p>
            </header>
        </div>
    )
}

export default RecentCard
